// 创建一个 PerformanceObserver 对象
const observer = new PerformanceObserver((list) => {
  const entries = list.getEntries()
  let totalBytes = 0
  let loadedBytes = 0
  // 遍历资源条目
  for (const entry of entries) {
    // 获取资源的总大小和已加载大小
    totalBytes += entry.transferSize
    loadedBytes += entry.encodedBodySize || entry.decodedBodySize
  }
  // 计算加载进度
  const progress = loadedBytes / totalBytes * 100
  console.log(`静态资源加载进度开始：${progress.toFixed(2)}%`)
})
// 开始监听资源条目
observer.observe({ entryTypes: ['resource'] })
// 获取页面加载过的资源
const resources = window.performance.getEntriesByType('resource')
let totalBytes = 0
let loadedBytes = 0
// 遍历资源条目
for (const resource of resources) {
  // 获取资源的总大小和已加载大小
  totalBytes += resource.transferSize
  loadedBytes += resource.encodedBodySize || resource.decodedBodySize
}
// 计算加载进度
const progress = loadedBytes / totalBytes * 100
console.log(`静态资源加载进度结束：${progress.toFixed(2)}%`)
