<template>
  <Dropdown :key="componentKey"/>
</template>

<script>
// 引入 Dropdown 组件
import Dropdown from '../components/HelloWorld.vue'

export default {
  name: 'HomeView',
  components: {
    Dropdown
  },
  data () {
    return {
      // 初始化时设置一个 key
      componentKey: Date.now()
    }
  },
  watch: {
    // 监听路由变化，当路由变化时更新 key
    '$route' (to, from) {
      this.resetComponent()
    }
  },
  methods: {
    resetComponent () {
      // 更新 key，以触发 Dropdown 组件的重载
      this.componentKey = Date.now()
    }
  }
}
</script>
