let timerId // 存储定时器ID

function setupCarousel () {
  const divElements = document.querySelectorAll('.home_head_bg div')
  const backgroundClasses = ['bg1', 'bg2', 'bg3', 'bg4']
  let globalIndex = 0

  function updateCarousel () {
    if (divElements.length === 0) {
      return // 如果没有找到元素，直接返回
    }
    for (let i = 0; i < divElements.length; i++) {
      divElements[i].classList.remove(...backgroundClasses)
      const newClassIndex = (globalIndex + i) % backgroundClasses.length
      divElements[i].classList.add(backgroundClasses[newClassIndex])
      console.log(divElements[i])
    }
  }

  function incrementIndex () {
    globalIndex = (globalIndex - 1 + backgroundClasses.length) % backgroundClasses.length
  }

  return { updateCarousel, incrementIndex }
}

export function initializeCarousel () {
  const { updateCarousel, incrementIndex } = setupCarousel()
  timerId = setInterval(function () {
    incrementIndex()
    updateCarousel()
  }, 3000) // 每3秒更新一次

  updateCarousel() // 初始调用一次以设置初始背景
}

export function destroyCarousel () {
  clearInterval(timerId)
}

export function restartCarousel () {
  destroyCarousel()
  initializeCarousel()
}
